import { RendererCanvas, remap } from '../../../lib';
import * as shapefile from 'shapefile'; // https://www.npmjs.com/package/shapefile

export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(divHost: any) {
        this.divHost = divHost;

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.width = '60%';
        div.style.height = '60%';
        this.divHost.appendChild(div);

        this.renderer = new Renderer(div);


    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

export class Renderer extends RendererCanvas {


    constructor(div: any) {
        super(div);
        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';

        // TODO
        // You code goes here for one time operation

        // console.log(shapefile)
        const path = 'https://raw.githubusercontent.com/NamjuLee/data/master/shape/cb_2018_us_county_5m/cb_2018_us_county_5m.shp';
        this.processShapefileData(path);

        this.start();
    }
    public async processShapefileData(path: string) {
        const shapefileData = await this.importingShape(path);
        if (shapefileData) {
            // console.log({shapefileData});
        } else {
            console.debug("empty shapefile");
        }
    }
    public async importingShape(path: string) {
        try {
            const source = await shapefile.open(path);
            const allData = [];
            let result;
            while ((result = await source.read()) && !result.done) {
                allData.push(result.value);
            }
            return allData;
        } catch (error) {
            console.error(error.stack);
            return null;
        }
    }


    public processData(d: any) {
        console.log(d);
    }
    public mouseDown(x: number, y: number): void {

    }
    public mouseUp(x: number, y: number): void {

    }
    public mouseDrag(x: number, y: number): void {

    }
    public render(ctx: CanvasRenderingContext2D): void {
        ctx.beginPath();
        ctx.arc(0, 0, 10, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();
    }
}